.vstr-exit-intent-popup {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999;
  background: rgba(33, 33, 33, 0.8);
  transform: translateY(60%) scale(0);
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);

  &.visible {
    transform: translateY(0) scale(1);
  }

  .popup {
    background: #5800ff;
    padding: 40px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    max-width: 400px;
    border-radius: 10px;
    box-sizing: border-box;
  }

  .popup .heading {
    font-size: 20px;
    color: inherit;
    line-height: 1.35em;
    margin: 0;
  }

  .popup .text {
    margin-top: 10px;
    color: inherit;
  }

  .popup .input {
    padding: 10px 20px;
    border: 1px solid #ddd;
    border-radius: 10px;
    flex: 1;
  }

  .popup .submit {
    background: rgba(0, 0, 0, 0.3);
    padding: 10px 16px;
    color: white;
    border-radius: 10px;
    cursor: pointer;
    outline: none;
    border: none;
    text-transform: initial;
    text-decoration: none;
    text-align: center;
    &[disabled]{
      opacity: 0.6;
    }
  }
.simple_loader {
  text-align: center;
  margin-top: 20px;
  display: flex;
  justify-content: center;
}
  .popup .form {
    display: flex;
    width: 100%;
    flex-direction: column;
    gap: 20px;
    margin-top: 20px;
  }

  .popup .close {
    cursor: pointer;
    width: 30px;
    height: 30px;
    margin: 0;
    line-height: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #3e03b2;
    border-radius: 10px;
    color: white;
    position: absolute;
    top: -10px;
    right: -10px;
    border-radius: 100%;
  }
}
